.cardsContainer {
  max-width: 1450px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: stretch;
  flex-wrap: wrap;
}

.findUserForm {
  display: flex;
  height: 150px;
  padding: 20px;
  margin-bottom: 20px;
  border: 2px solid #a2836e;
  border-radius: 10px;
  color: #fff;

  &__genderSelectLabel {
    margin-bottom: 10px;
  }

  &__natSelectLabel {
    margin-bottom: 10px;
  }

  &__genderSelect {
    display: flex;
    flex-direction: column;
    margin-right: 30px;
  }

  &__natSelect {
    display: flex;
    flex-direction: column;
    margin-right: 10px;
  }

  &__natSelectInput {
    width: 200px;
  }

  &__submitButton {
    height: 30px;
  }
}
