.userCard {
  width: 220px;
  display: flex;
  flex-direction: column;
  margin-bottom: 25px;
  padding: 20px;
  border: 2px solid #1E1E1E;
  border-radius: 10px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 1);
  background-color: #6C7A7D;
  transition-duration: 0.3s;

  &:hover {
    box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 1);
    transform: scale(1.1);
  }

  &__img-container {
    margin: 0 auto;
    box-sizing: border-box;
  }

  &__img {
    width: 150px;
    height: 150px;
    border-radius: 10px;
  }

  &__title {
    font-size: 20px;
    color: #ccc;
    font-weight: 700;
    align-self: center;
  }

  &__info {
    color: #333333;
    display: flex;
    flex-direction: column;
    justify-content: center;
    font-size: 16px;

    &-gender {
      margin-bottom: 5px;
      width: max-content;
      align-self: flex-end;
    }

    &-email{
      font-size: 14px;
      margin-bottom: 5px;
    }

    &-dob {
      margin-bottom: 5px;
      width: max-content;
      align-self: flex-end;
    }
  }
}
